

.Nav{
    border: 1px solid red;
    height: 50px;
}

.secondNav{
    border: 1px solid red;
    background-color: rgb(216, 159, 73);
    height: 50px;
    display: none;
}
.ThirdNav{
    border: 1px solid red;
    height: 50px;
    display: none;
}


@media only screen and (min-width:421px) and (max-width: 820px) {
    .secondNav{
        display: block;
        display: flex;
    }
    .Nav{
        display: none;
    }
    .menu{
        /* border: 1px solid blue; */
        color: teal;
        border: none;
        width: fit-content;
          display: flex;
          margin-left: 90%;
          margin-right: 5%;
          justify-content: center;
          align-items: center;
    }
  }

  @media only screen and (max-width: 420px) {
    .secondNav{
        display: none;
    }
    .Nav{
        display: none;
    }
    .ThirdNav{
        display: block;
    }
  }
  
  